.OptionButton {
    width: 65px;
    height: 30px;
    margin-left: 10px;
    color: #fff;
    background-color: #00008b;
    border: 1px solid #fff;
    border-radius: 15px;
}

.OptionButton:hover {
    background-color: aliceblue;
    color: #000;
    border-color: darkblue;
}

.OptionButton:first-child {
    margin-left: 0;
}