.List {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
}

.ListItem {
    display: flex;
    justify-content: space-between;
    margin-bottom: 7px;
}

.Value {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    border: 1px solid black;
    background-color: navy;
    color: #fff;
}
